<template>
  <div class="address">
    <van-nav-bar
            title="添加收货地址"
            :fixed="true"
            left-arrow
            @click-left="$router.back()"
    >
    </van-nav-bar>
    <div class="content">
      <van-cell-group>
        <van-field v-model="addressInfo.name" label="姓名" placeholder="请输入姓名" required></van-field>
        <van-field v-model="addressInfo.tel" label="电话" placeholder="请输入电话"
                   required type="number"></van-field>
        <van-field v-model="addressInfo.zipCode" label="邮编" placeholder="请输入邮编"
                   type="number"></van-field>
        <van-field
                required
                class="address-textarea"
                v-model="addressInfo.detail"
                type="textarea"
                label="具体地址"
                placeholder="请输入省市区县具体地址"
        ></van-field>
      </van-cell-group>
      <van-button size="large" type="primary" @click="onSave">保存</van-button>
      <van-button size="large" type="danger" @click="deleteAddress" v-if="ADDRESS_INFO !== ''">删除</van-button>
    </div>
  </div>
</template>

<script>
  import {mapGetters, mapMutations} from "vuex";

  export default {
    name: "receiveAddress",
    data() {
      return {
        addressInfo: {
          id:'',
          name: '',
          tel:'',
          zipCode:'',
          detail: ''
        },
      };
    },
    mounted() {
      if (this.ADDRESS_INFO !== "") {
        this.addressInfo = {
          id: this.ADDRESS_INFO.ID,
          name: this.ADDRESS_INFO.RecvUserName,
          tel: this.ADDRESS_INFO.LinkPhone,
          detail: this.ADDRESS_INFO.Address,
          zipCode: this.ADDRESS_INFO.ZipCode
        };
      }

      if (/Android/gi.test(navigator.userAgent)) {
        // 检测userAgent是否为Android
        window.addEventListener('resize', function () {
          if (document.activeElement.tagName == 'INPUT' || document.activeElement.tagName == 'TEXTAREA') {
            // 判断当前active的元素是否为input/ textarea
            window.setTimeout(function () {
              document.activeElement.scrollIntoView()
              // 原生方法，滚动至需要显示的位置
            }, 0)
          }
        })
      }
    },
    beforeDestroy() {
      this.saveAddressInfo("");
    },
    computed: {
      ...mapGetters(["ADDRESS_INFO"])
    },
    methods: {
      ...mapMutations({changeAddressID: "CHANGE_ADDRESS"}),
      ...mapMutations({saveAddressInfo: "SAVE_ADDRESS_INFO"}),
      onSave() {
        console.log(this.addressInfo);
        if(this.addressInfo.name.trim() === ''){
          this.$toast('姓名不能为空')
          return
        }
        let telReg = /^1(?:3\d|4[4-9]|5[0-35-9]|6[67]|7[013-8]|8\d|9\d)\d{8}$/
        if(!telReg.test(this.addressInfo.tel)){
          this.$toast('请输入正确的手机号')
          return
        }
        let zipReg = /^[0-9]{6}$/
        if(this.addressInfo.zipCode){
          if(!zipReg.test(this.addressInfo.zipCode)){
            this.$toast('邮编格式不正确')
            return
          }
        }
        if (this.addressInfo.detail.length >= 120) {
          this.$toast('地址超过120字限定长度')
          return
        }
        if(this.addressInfo.detail.trim() === ''){
          this.$toast('具体地址不能为空')
          return
        }
        axios
          .post("/api/v1/Reg/ExchangePostAddrSave", {
            ID: this.ADDRESS_INFO.ID || 0,
            Address: this.addressInfo.detail,
            RecvUserName: this.addressInfo.name,
            LinkPhone: this.addressInfo.tel,
            ZipCode: this.addressInfo.zipCode || '',
            Province:'',
            City:'',
            District:''
          })
          .then(res => {
            if (res.data.State === 1) {
              this.$toast("保存成功");
              this.changeAddressID(res.data.Data);
              setTimeout(() => {
                this.$toast.clear();
                this.$bus.emit("addAddress");
                this.$router.back();
              }, 500);
            }
            console.log(res.data);
          })
          .catch(e => {
            console.log(e);
          });
      },
      deleteAddress() {
        axios
          .get(
            `/api/v1/Reg/ExchangePostAddrDel?postAddrId=${this.ADDRESS_INFO.ID}`
          )
          .then(res => {
            console.log(res.data);
            if (res.data.State === 1) {
              this.$toast("删除成功");
              setTimeout(() => {
                this.$toast.clear();
                this.$router.back();
                this.$bus.emit("addAddress");
              }, 400);
            }
          });
      }
    }
  };
</script>

<style scoped lang="scss">
  .address {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 100%;
    z-index: 501;
    background-color: #fff;
    .content {
      padding-top: 46px;
    }
    /deep/ .van-button--large{
      margin-bottom: 1rem;
    }
  }

  /deep/ .van-address-edit {
    margin-top: 46px;
  }
</style>
